const btn = document.querySelector("#todo-btn");
const header = document.querySelector("header");

const todoShow = document.querySelector("#todo-show");
const todoForm = document.getElementById("todo-form");
const todoInput = todoForm.querySelector("#todo-form input");
const todoList = document.getElementById("todo-list");

const TODOS_KEY = "todos";

let toDos = [];

function saveToDos() {
  localStorage.setItem(TODOS_KEY, JSON.stringify(toDos)); // localstorage ì— toDosë¥¼ ë¬¸ìì—´í˜•íƒœë¡œ ì €ì¥(stringify)
}

function deleteTodo(event) {
  const li = event.target.parentElement; // í´ë¦­ëœ (event) target (ë²„íŠ¼) ì˜ parentElement(ë¶€ëª¨ìš”ì†Œ (text)) ë¥¼ li ì— ì €ì¥í›„ ì‚­ì œ
  li.remove();
  toDos = toDos.filter((toDo) => toDo.id !== parseInt(li.id));
  saveToDos();
}

function paintToDo(newTodo) {
  const li = document.createElement("li"); // ìš”ì†Œ ì¶”ê°€ í•˜ê¸°
  li.id = newTodo.id;
  const span = document.createElement("span");
  span.innerText = newTodo.text; // spanì˜ ë‚´ìš© ë¶ˆëŸ¬ì™€ì„œ ë°”ê¾¸ê¸°
  const button = document.createElement("button");
  button.innerText = " X";
  button.addEventListener("click", deleteTodo);
  li.appendChild(span); // ìš”ì†Œì•„ë˜ì— ìš”ì†Œ ë‹´ê¸°
  li.appendChild(button);
  todoList.appendChild(li);
}

function handleToDoSubmit(event) {
  event.preventDefault(); // ìƒˆë¡œê³ ì¹¨ì‹œ ì‚¬ë¼ì§€ëŠ”ê±° ë§‰ê¸°
  const newTodo = todoInput.value; // inputì˜ ê°’ ì €ì¥ í•˜ê¸°
  todoInput.value = ""; // inputì— ì—”í„°ì‹œ ê°’ ì‚¬ë¼ì§€ê²Œ í•˜ê¸°
  const newTodoObj = {
    text: newTodo,
    id: Date.now(),
  };
  toDos.push(newTodoObj);
  paintToDo(newTodoObj); // ì‹¤í–‰ì‹œ ë§ˆë‹¤ paintToDo ì‹¤í–‰í•˜ê¸°
  saveToDos();
}

todoForm.addEventListener("submit", handleToDoSubmit); // form ì— ì—”í„°ë¡œ ì œì¶œ ì‹œë§ˆë‹¤ handeltodosubmit í•¨ìˆ˜ ì‹¤í–‰í•˜ê¸°

const savedToDos = localStorage.getItem(TODOS_KEY);

if (savedToDos !== null) {
  const parsedToDos = JSON.parse(savedToDos);
  toDos = parsedToDos;
  parsedToDos.forEach(paintToDo);
}

function showTodo(event) {
  let tmp = todoShow.style.display;
  if (tmp === "none") {
    tmp = "block";
    header.style.backgroundColor = "white";
    btn.innerText = "Close";
  } else {
    tmp = "none";
    header.style.backgroundColor = "";
    btn.innerText = `To_do[ ${toDos.length} ]`;
  }
  todoShow.style.display = tmp;
}
btn.addEventListener("click", showTodo);
btn.innerText = `To_do[ ${toDos.length} ]`;
